.footer {
    width: 100%;
    height: 571px;
    background-image: url("../../assets/footer-bg.png");
    background-size: cover;
    background-position: center;
    margin-top: 50px;
    position: relative;
}

.idoSell {
    width: 101px;
    height: 23px;
    background-image: url("../../assets/idoSell.png");
    position: absolute;
    font-size: 0;
    bottom: 23px;
    left: 0; 
    right: 0; 
    margin-left: auto; 
    margin-right: auto;
}

.container {
    width: 1170px;
    margin: auto;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 80px;
    color: #aaaaaa;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    display: inline-flex;
}

.section {
    border-right: 1px solid #333;
}

.section:last-child {
    border: none;
}

.informations {
    margin-left: 8px;
    width: calc(222px - 78px);
    padding-right: 70px;
}

.informationsTitle, .myAccountTitle, .contactTitle, .ordersTitle {
    color: #fff;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 16px;
}

.informationsText, .myAccountText, .ordersText {
    height: 30px;
    display: flex;
    align-items: center;
}

.informationsLink, .myAccountLink, .ordersLink {
    cursor: pointer;
}

.informationsLink:hover, .myAccountLink:hover, .ordersLink:hover {
    color: #b62323;
}

.myAccount {
    margin-left: 88px;
    width: calc(313px - 192px);
    padding-right: 104px;
}

.contact {
    margin-left: 70px;
    width: calc(354px - 170px);
    padding-right: 100px;
    color: #fff;
}

.contactNumber {
    font-size: 20px;
    margin-bottom: 5px;
}

.contactEmail {
    font-size: 14.5px;
    margin-bottom: 30px;
}

.socials {
    display: inline-flex;
    gap: 30px;
    height: 25px;
}

.social {
    height: 100%;
    cursor: pointer;
}

.orders {
    margin-left: 81px;
    width: calc(278px - 81px);
}

.ordersText {
    gap: 0 12px;
}

@media only screen and (max-width: 1200px) {
    .footer {
        margin-top: 0;
    }

    .container {
        width: 770px;
    }

    .informations {
        width: 145px;
        margin-left: 0;
        padding-right: 20px;
    }

    .myAccount {
        width: 145px;
        margin-left: 20px;
        padding-right: 20px;
    }

    .contact {
        width: 155px;
        margin-left: 20px;
        padding-right: 20px;
    }

    .orders {
        margin-left: 20px;
        width: 202px;
    }
}

@media only screen and (max-width: 800px) {
    .footer {
        width: 100%;
    }

    .container {
        width: 90%;
        justify-content: center;
        gap: 0 16px;
    }

    .section {
        display: none;
        border: none;
    }

    .section:first-child, .section:last-child {
        display: block;
    }

    .informations {
        padding: 0;
        width: auto;
    }

    .orders {
        margin: 0;
        width: auto;
    }

    .ordersText div:first-child {
        font-size: 0;
    }

    .informationsText, .ordersText {
        height: auto;
        margin: 5px 0;
    }
}