.TopBar {
    height: 36px;
    width: 100%;
    background-color: #2e2e2e;
}

.content {
    width: 1170px;
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 11px;
    margin: auto;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.TopBar img {
    margin-right: 10px;
    vertical-align: middle;
}

.item {
    display: inline-block;
    margin-right: 40px;
}

@media only screen and (max-width: 1200px) {
    .content {
        width: 770px;
    }
}